import React from 'react';
import { Dialog, DialogTitle, DialogContent, Paper, TextField, DialogActions, Button, CircularProgress } from '@material-ui/core';
import { DatePicker } from "material-ui-pickers";
import { Close, Save } from '@material-ui/icons';
import Select from 'react-select';

const styles = {
    select : {
        fontSize: '0.75rem',
        color   : '#f11111',
    }
}

interface Add{
    modalAdd                    :   boolean,
    updateInput                 :   any,
    productInput                :   any,
    submit                      :   boolean,
    responseMessage             :   any,
    supplierState               :   any,
    setProductInput             :   any,


    setModalAdd(a:boolean)      :   void,
    submitForm(a:any,b:string)  :   void,
}

const Add = (props:Add) => {


    console.log(props.productInput);

    if(props.modalAdd){
        let options;
        if(props.supplierState){
            options = props.supplierState.data.data.map((value:any) => {
                return {
                    label : value.supplier_code + " ( "+ value.supplier_name +" ) ",
                    value : value.id,
                    target: {
                        name  : 'supplier',
                        value : value.id 
                    }
                }
            })
        }else{
            options = {
                label : '',
                value : '',
                target: {
                    name : 'supplier',
                    value : ''
                }
            }
        }

        let status = [
            {
                label : 'New',
                value : 1,
                target: {
                    name : 'status',
                    value : 1,
                }
            },{
                label : 'Replaced',
                value : 2,
                target: {
                    name : 'status',
                    value : 2,
                }
            },{
                label : 'Returned',
                value : 3,
                target: {
                    name : 'status',
                    value : 3,
                }
            }
        ];

        return(
            <Dialog
                fullWidth
                maxWidth="sm"
                open={props.modalAdd}
                onClose={ () =>props.setModalAdd(false) }
                aria-labelledby="max-width-dialog-title"
            >
                <DialogTitle id="max-width-dialog-title" style={{textAlign:'center'}}>Add New Product</DialogTitle>
                <DialogContent>

                    <Paper className="text-center">
                        <form className ="form-group" onSubmit={(event:any) => props.submitForm(event,"add")} id="submit-edit-form">
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Supplier
                                    </div>
                                    <div className="col-md-8">
                                        <Select
                                            options={options}
                                            // isClearable
                                            name = 'supplier'
                                            // defaultValue = {options[0]}
                                            onChange = {props.updateInput}
                                            isDisabled = {props.submit}
                                            className={props.responseMessage.supplier.error? "select-error" : "" }
                                            required
                                        />
                                        <div hidden={!props.responseMessage.supplier.error} style = {styles.select}>{props.responseMessage.supplier.message}</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Product Code
                                    </div>
                                    <div className="col-md-8">
                                        <TextField
                                            type="text"
                                            margin="normal"
                                            variant="outlined"
                                            className="full-width product-reduce-margin-text"
                                            name = "product_code"
                                            onChange = {props.updateInput}
                                            value = {props.productInput.product_code}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.product_code.error}
                                            helperText = {props.responseMessage.product_code.message}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Product Name
                                    </div>
                                    <div className="col-md-8">
                                        <TextField
                                            margin="normal"
                                            variant="outlined"
                                            className="full-width product-reduce-margin-text"
                                            name = "product_description"
                                            onChange = {props.updateInput}
                                            value = {props.productInput.product_description}
                                            disabled = {props.submit}
                                            error = {props.responseMessage.product_description.error}
                                            helperText = {props.responseMessage.product_description.message}
                                            required
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Serial Number
                                    </div>
                                    <div className="col-md-8">
                                        <TextField
                                            margin="normal"
                                            variant="outlined"
                                            className="full-width product-reduce-margin-text"
                                            name = "serial_number"
                                            onChange = {props.updateInput}
                                            value = {props.productInput.serial_number}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.serial_number.error}
                                            helperText = {props.responseMessage.serial_number.message}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Status
                                    </div>
                                    <div className="col-md-8">
                                        <Select
                                            options={status}
                                            // isClearable
                                            name = 'status'
                                            // defaultValue = {options[0]}
                                            onChange = {props.updateInput}
                                            isDisabled = {props.submit}
                                            className={props.responseMessage.status.error? "select-error" : "" }
                                            required
                                        />
                                        <div hidden={!props.responseMessage.status.error} style = {styles.select}>{props.responseMessage.status.message}</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Delivery Date
                                    </div>
                                    <div className="col-md-8">
                                        <DatePicker
                                            format="dd/MM/yyyy"
                                            allowKeyboardControl
                                            animateYearScrolling
                                            variant = "outlined"
                                            className = "full-width product-reduce-margin-text"
                                            onChange = {(event) => props.updateInput(event,"delivery_date")}
                                            value = {props.productInput.delivery_date}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.delivery_date.error}
                                            helperText = {props.responseMessage.delivery_date.message}
                                            // defaultValue={new Date()}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Delivery Doc.
                                    </div>
                                    <div className="col-md-8">
                                        <TextField
                                            type="text"
                                            margin="normal"
                                            variant="outlined"
                                            className="full-width product-reduce-margin-text"
                                            name = "reference_delivery_document"
                                            onChange = {props.updateInput}
                                            value = {props.productInput.reference_delivery_document}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.reference_delivery_document.error}
                                            helperText = {props.responseMessage.reference_delivery_document.message}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Warranty(*year)
                                    </div>
                                    <div className="col-md-8">
                                        <TextField
                                            type="number"
                                            margin="normal"
                                            variant="outlined"
                                            className="full-width product-reduce-margin-text"
                                            name = "warranty"
                                            onChange = {props.updateInput}
                                            value = {props.productInput.warranty}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.warranty.error}
                                            helperText = {props.responseMessage.warranty.message}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Warranty Start
                                    </div>
                                    <div className="col-md-8">
                                        <DatePicker
                                            format="dd/MM/yyyy"
                                            allowKeyboardControl
                                            animateYearScrolling
                                            variant = "outlined"
                                            className = "full-width product-reduce-margin-text"
                                            onChange = {(event) => props.updateInput(event,"warranty_start")}
                                            value = {props.productInput.warranty_start}
                                            // minDate = {new Date(props.productInput.delivery_date)}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.warranty_start.error}
                                            helperText = {props.responseMessage.warranty_start.message}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="row align-items-center">
                                    <div className="col-md-4 font-bolder">
                                        Remarks
                                    </div>
                                    <div className="col-md-8">
                                        <TextField
                                            // type="number"
                                            margin="normal"
                                            variant="outlined"
                                            className="full-width product-reduce-margin-text"
                                            name = "remarks"
                                            onChange = {props.updateInput}
                                            value = {props.productInput.remarks}
                                            disabled = {props.submit}
                                            required
                                            error = {props.responseMessage.remarks.error}
                                            helperText = {props.responseMessage.remarks.message}
                                        />
                                    </div>
                                </div>
                            </div>
                            <input type="submit" style={{display:'none'}} />
                        </form>
                    </Paper>

                </DialogContent>
                <DialogActions>

                    <Button onClick={() =>props.setModalAdd(false)} color="secondary" variant="contained">
                        <Close />   
                        Cancel
                    </Button>

                    <Button 
                        onClick={(event:any) => props.submitForm(event,"add")} 
                        color="primary" 
                        variant="contained"
                        disabled={props.submit}
                        form="submit-edit-form"
                    >  
                        <Save />
                        Submit
                        <CircularProgress style={{display: !props.submit ? 'none' : 'unset' }} className="product-circular-progress"/>
                    </Button>
                   
                </DialogActions>
            </Dialog>
        );
    }else{
        return null;
    }
}

export default Add;